@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Inter', sans-serif;
}
.HeaderDiv{
    display: flex;
gap:60px;

}
.HeaderLogoBlock{
    display: flex;
}
.HeaderLogoLink{
    
    font-size: 20px;
    font-weight: 500;
    color:#5A5B6A;
    display: flex;
    align-items: center;

}
.HeaderRegisterLink{
    font-size: 20px;
    font-weight: 400;
    color:#5A5B6A;
    
    
    
}
.HeaderInput{
    
    
    width: 328px;
    height: 48px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: #EDEDF0;
    border :1px solid #EDEDF0;
    border-radius: 99px;
    color: #333;
    margin-top: 15px;
position: relative;
outline: none;

}
.ButtonBlock{
    display: flex;
gap: 60px;
margin-top: 15px;
}
.headersearchicon  {
width: 17.5px;
height: 17.5px;
position:absolute;
left: 21%;
top:32px
}
.headerclearicon{
    width: 17.5px;
    height: 17.5px;
    position:absolute;
    left: 44%;
    top: 32px;
}
.HeaderButtonLink{
    font-size: 20px;
    font-weight: 400;
    color:#5A5B6A;

margin-top: 15px;
}
.HeaderSigninButton
{
font-size: 20px;
font-weight: 700;
color: #2264D1;
border: 1px solid #2264D1;
background-color: #FFFFFF;
border-radius: 4px;
width: 104px;

height: 40px;
}
.HeaderMyCartButton
{
font-size: 20px;
font-weight: 700;
color: #2264D1;
border: 1px solid #2264D1;
background-color: #FFFFFF;
border-radius: 4px;
width: 99px;

height: 40px;
}